/*
*
* Buttons
* --------------------------------------------------
*/


// Button General Styles

.button, .btn {
  display: inline-block;
  position: relative;
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(13px, 25px, 14px, 20px, 0);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-style: solid;
  border-width: 1px;
  letter-spacing: .15em;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  text-transform: uppercase;
  font-weight: 600;
  user-select: none;
  transition: 250ms all ease-in-out;

  &:focus,
  &:active,
  &:active:focus {
    outline: none;
  }

  &.disabled{
    color: black;
    background-color: gray;
  }

  .caret{
    margin-left: 8px;
  }
  
  &:before, &:after{
    position: absolute;
    top: 50%;
    content: '';
    background: $primary;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    transform: translateY(-50%);
    transition: 250ms all ease-in-out;
  }
  &:before{
    left: 15px;
  }
  &:after{
    right: 15px;
  }
}

.button-block{
  display: block;
  width: 100%;
}

.btn{
  &:before, &:after{
    display: none;
  }
}

// Appearance style
// --------------------------------------------------

.button-default {
  @include btn-variant($gray-darker, transparent, $gray, $white, $gray-darker, $gray-darker);
}

.button-white {
  @include btn-variant($white, transparent, $white, $primary-, $white, $white);
  &:before, &:after{
    background: $white;
    
  }
  &:hover{
    &:before, &:after{
      background: $primary-;
    }

  }
}

.button-primary, .btn-primary {
  @include btn-variant($primary-, transparent, $primary-, $white, $primary-, $primary-);
  
  &:hover{
    &:before, &:after{
      background: $white;
    }
  }
}

.btn-primary:active:hover, .btn-primary:active:focus, .btn-primary:active.focus, .btn-primary.active:hover, .btn-primary.active:focus, .btn-primary.active.focus, .open > .btn-primary.dropdown-toggle:hover, .open > .btn-primary.dropdown-toggle:focus, .open > .btn-primary.dropdown-toggle.focus{
  color: $white;
  background-color: $primary-;
  border-color: $primary-;
}

.button-primary-type-2 {
  @include btn-variant($white, $primary-, $primary-, $primary-, $white, $primary-);

  &:before, &:after{
    background: $white;
  }
  
  &:hover{
    &:before, &:after{
      background: $primary-;
    }
  }
  
}

.button-steel-blue {
  @include btn-variant($white, #2f6890, #2f6890, $white, darken(#2f6890, 10%), darken(#2f6890, 10%));
}

.button-cerulean {
  @include btn-variant($white, #14a5eb, #14a5eb, $white, darken(#14a5eb, 5%), darken(#14a5eb, 5%));
}

.button-mandy {
  @include btn-variant($white, #e75854, #e75854, $white, darken(#e75854, 10%), darken(#e75854, 10%));
}

// Btn with shadow
.button-shadow{
  box-shadow: 0 3px 11px 0 rgba(0, 0, 0, 0.15);

  &:hover{
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.15);
  }
}


// Button Sizes
// --------------------------------------------------

.button-xs, .btn-sm {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(5px, 20px, 14px, 22px, 0);
}

.button-sm {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(10px, 22px, 14px, 20px, 0);
  letter-spacing: .075em;

  
}

.button-lg {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(18px, 30px, 18px, 22px, 0);
  letter-spacing: .1em;
  font-weight: 400;
  &:before{
    left: 20px;
  }
  &:after{
    right: 20px;
  }
}

.button-xl{
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(23px, 30px, 18px, 22px, 0); 
  letter-spacing: .1em;
  &:before{
    left: 20px;
  }
  &:after{
    right: 20px;
  } 
}


// Button Shapes
// --------------------------------------------------

.button-circle {
  border-radius: 30px;
}

.button-ellipse {
    border-radius: 5px;
}


// Button Icon styles
// --------------------------------------------------

.button {
  &.button-icon {
    padding-left: 17px;
    padding-right: 17px;
    
    &:before, &:after{
      display: none;
    }

    .icon {
      font-size: 18px;
      line-height: inherit;
      vertical-align: middle;
      transition: 0s;
    }

    &-left {
      .icon {
        float: left;
        padding-right: 10px;
      }
    }

    &-right {
      .icon {
        float: right;
        padding-left: 10px;
      }
    }
  }
}

.group-sm{
  .button{
    vertical-align: bottom;
  }
}

.button-block-right-line{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &:after{
    content: '';
    width: 90px;
    border-top: 1px solid $primary-;
    display: none;
    @media (min-width: $screen-sm-min) {
      display: block;   
    }
  }
  
}

.button-block-right-line.button-block-sm-center{
  @media (min-width: $screen-sm-min) and (max-width: 991px) {
    justify-content: center;
  }
}
.button-block-line{
  display: flex;
  justify-content: center;
  align-items: center;
  &:after, &:before{
    content: '';
    width: 90px;
    border-top: 1px solid $primary-;
    display: none;
    @media (min-width: $screen-sm-min) {
      display: block;   
    }
  }
  
}

h4{
  .button{
    
    font-size: 20px;
    font-weight: 600;
    padding: 8px 25px;
    letter-spacing: .1em;
    @media (min-width: $screen-sm-min) {
      font-size: 24px;
      letter-spacing: .15em;
    }
  }
}