/*
*
* Text styling
* --------------------------------------------------
*/


// Text style
//

.text-italic {
  font-style: italic;
}

.text-normal {
  font-style: normal;
}

.text-date-type{
  display: flex;
  justify-content: center;
  align-items: center;
  &:before, &:after{
    margin-left: 10px;
    margin-right: 10px;
    content: '';
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: $gray-dark-2;
  }
}

.link-img{
  transition: all .3s;
  will-change: transform;
  
  img{
    transition: all .3s;
  }
  &:hover{
    img{
      transform: scale(1.02);
    }
    
  }
}

.text-dot{
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  &:before, &:after{
      position: absolute;
      top: 50%;
      content: '';
      background: $primary;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      transform: translateY(-50%);
      transition: 250ms all ease-in-out;
  }
  &:before{
    left: 3px;
  }
  &:after{
    right: 3px;
  }
}
.text-dot-type-2{
  display: inline-block;
  padding-left: 35px;
  padding-right: 35px;
  position: relative;
  &:before, &:after{
    position: absolute;
    top: 50%;
    content: ':';
    line-height: 1;
   
    transition: 250ms all ease-in-out;
  }
  &:before{
    left: 0;
    transform: translateY(-50%) rotate(90deg);
  }
  &:after{
    right: 0; 
    transform: translateY(-50%) rotate(-90deg);
    
  }
  
}

// Text decoration
//

.text-underline {
  text-decoration: underline;
}

.text-strike {
  text-decoration: line-through;
}

// Text weight
//

.text-thin {
  font-weight: 100;
}

.text-light {
  font-weight: 300;
}

.text-regular{
  font-weight: 400;
}

.text-medium{
  font-weight: 500;
}

.text-sbold{
  font-weight: 600;
}

.text-bold, strong {
  font-weight: 700;
}

.text-ubold {
  font-weight: 900;
}


// Text spacing 
//

.text-spacing-0{
  letter-spacing: 0;
}

.text-spacing-75{
  letter-spacing: .075em;
}

.text-spacing-200{
  letter-spacing: .2em;
}

// Text opacity
//

.text-opacity-06{
  opacity: .6;
}