/*
*
* NAVBAR HOME
* --------------------------------------------------
*/

.rd-navbar.rd-navbar-home{
  background: $secondary-;
  
  &.rd-navbar-fullwidth{
    .rd-navbar-inner{
      background: $secondary-;
      max-width: 100%;
      padding-left: 0;  
      padding-right: 0;
      padding-bottom: 0;
      .rd-navbar-aside-right{
        background: $gray-base;
        padding-top: 10px;
        padding-bottom: 13px;
      }
    }
    .rd-navbar-nav{
      >li + li{
        margin-left: 40px;
      }
      >li{
  
        &:after{
          position: absolute;
          display: block;
          top: 50%;
          right: -22px;
          content: '';
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: $primary;
          transform: translateY(-50%);
        }
        &:last-child{
          &:after{
            display: none;
          }
        }
        >a{
          font-size: 14px;
          line-height: 20px;
          color: $white;
          text-transform: uppercase;
          letter-spacing: .15em;
          &:hover{
            color: $gray;
          }
          
        }
        &.focus, &.active{
          >a{
            color: $gray;
          }
        }
        .rd-navbar-submenu-toggle{
          display: none;
        }
      }
    }

    .rd-navbar-dropdown, .rd-navbar-megamenu{
      background: $secondary-;
      a{
        color: $primary;
        &:hover{
          color: #fdfdfb;
          background: transparent;
        }
      }
    }
    .rd-navbar-megamenu{
      .mega-menu-title{
        color: $white;
      }
    }
   
    
    // navbar stuck
    //
    &.rd-navbar--is-stuck{
      .rd-navbar-inner{
        padding: 0;
        
      }
    }
  }
  //  navbar fixed
  //  
  &.rd-navbar-fixed{
    .rd-navbar-panel{
      &:before{
        background: $secondary-;
      }
    }
    .rd-navbar-brand{
      width: 100%;
      text-align: center;
      .brand-name{
        display: inline-block;
        transform: translateX(-21px);
      }
    }
    .rd-navbar-toggle{
      span, span:after, span:before{
        background-color: $white;
      }
    }
    .rd-navbar-nav {
      li{
        > a{
          color: $secondary-;
          &:hover{
          color: $white;
        }
        }
        &.active, &.opened{
          > a{
            color: $white;
          }
         
        }
      }
    }
  }
}

