/*
*
* Box-custom
* --------------------------------------------------
*/

.box-info{
  text-align: left;
  .box-title{
    a{
      text-transform: uppercase;
      color: $gray;
      font-size: 18px;
      &:hover{
        color: $gray;
        text-decoration: underline;
      }
    }
    * + p{
      margin-top: 0;
    }
  }
  .box-title + *{
    margin-top: 25px;
  }
  .table-info{
    color: $white; 
    
    tr{
      td:first-child{
        color: $primary;
      }
      td{
        font-size: 18px;
        line-height: 24px;
        padding-right: 15px;
        vertical-align: top;
      }
    }
  }
}

// box-sponsor
//

.box-sponsor-type-1{
  
  @media (min-width: $screen-sm-min) {
    text-align: left;   
  }

  * + h5{
    margin-top: 20px;
    @media (min-width: $screen-md-min){
      margin-top: 25px;
    }
  }
  
  h5{
    a{
      color: $secondary-;
      &:hover{
        color: $primary-;
      }
    }
  }
  
  @media (min-width: $screen-lg-min){
    .unit-md-horizontal > .unit__left + .unit__body{
      padding-left: 35px;
    }
  }
}
.box-sponsor-type-2{
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  
  > * + *{
    margin-top: 20px;
    @media (min-width: $screen-md-min){
      margin-top: 30px;
    }
  }
  .img-block{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 160px;
  }
  h5{
    a{
      color: $white;
      &:hover{
        color: $primary;
      }
    }
  }
}
.box-sponsor-type-3{
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  padding: 25px;
  background: $gray-lighter;
  
  @media (min-width: $screen-sm-min) {
    padding: 35px;   
  }
  
  .img-block{
    min-height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .img-block + *{
    margin-top: 35px;
  }
  h5{
    a{
      color: $secondary-;
      &:hover{
        color: $primary-;
      }
    }
  }
}

// box team
//

.box-team-type-1{
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
  
  >* + *{
    margin-top: 20px;
    @media (min-width: $screen-md-min){
      margin-top: 30px;
    }
  }
 
  .name{
    font-weight: 600;
    color: $secondary-;
    a{
      font-weight: 600;
      color: $secondary-;
      &:hover{
        color: $primary-;
      }
    }
    * + p{
      margin-top: 0;
    }
  }
  
  &-modern{
    max-width: 370px;
    background: $gray-lighter;
    padding: 25px;
    @media (min-width: $screen-sm-min) {
      padding: 40px;;   
    }
    .icon{
      color: $gray-light-2;
      transition: all .3s;
      &:hover{
        color: $primary-;
      }
    }
  }
}

.box-team-type-2{
  >* + *{
    margin-top: 20px;
    @media (min-width: $screen-sm-min) {
      margin-top: 30px;   
    }
  }

  .name{
    font-weight: 600;
    color: $white;
    a{
      font-weight: 600;
      color: $white;
      &:hover{
        color: $primary-;
      }
    }
    * + p{
      margin-top: 0;
    }
  }
  
  .icon{
    color: $gray-light-2;
    transition: all .3s;
    &:hover{
      color: $primary-;
    }
  }
}

.box-team-info{
  text-align: left;
  * + .contact-block{
    margin-top: 30px;
  }
  .contact-block{
    .unit + .unit{
      margin-top: 15px;
    }
    a{
      font-weight: 600;
      color: $gray;
      &:hover{
        color: $primary-;
      }
    }
    .link-block{
      a[href*='callto']{
        display: inline-block;
        margin-right: 5px;
        &:after{
          content: ',';
        }
        &:last-child{
          &:after{
            display: none;
          }
        }
        &:last-child{
          margin-right: 0;
        }
        &:hover{
          &:after{
            color: inherit;
          }
        }
      }
    }
  }
  h4 + h5{
    margin-top: 15px;
  }
  * + p{
    margin-top: 20px;
  }
}

// box pricing
//

.box-pricing-type-1{
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid $primary-;
  
  .box-header{
    padding: 20px 10px;
    border-bottom: 1px solid $primary-;
    
    @media (min-width: $screen-sm-min) {
      padding: 30px 10px;   
    }
    
    h3{
      font-weight: 400;
     
      @media (min-width: $screen-md-min){
        white-space: nowrap;
      }
      > span{
        font-family: $font-family-base;
        font-size: 30px;
        display: inline-block;
        margin-left: -5px;
        
        >span{
          font-size: 14px;
          text-transform: uppercase;
          letter-spacing: .1em;
          margin-left: -5px;
        }
      }
    }
  }
  
  
  .box-body{
    padding: 25px 25px 65px;
    position: relative;
    min-height: 255px;
    
    @media (min-width: $screen-sm-min) {
      padding: 35px 35px 70px;   
    }
    
    .list{
      font-size: 18px;
      line-height: 24px;
      letter-spacing: 0;
    }
   
    .button{
      position: absolute;
      bottom: -1px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  
}

.box-pricing-type-2{
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  * + .button-block{
    margin-top: 25px;
  }
}

.box-pricing-type-3{
  position: relative;
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  padding: 30px 25px ;
  border: 1px solid $primary-;
  
  h3{
    font-weight: 400;
    
    > span{
      font-family: $font-family-base;
      font-size: 30px;
      display: inline-block;

      >span{
        font-size: 14px;
        text-transform: uppercase;
        letter-spacing: .1em;
      }
    }
  }
  
  * + .box-body{
    margin-top: 25px;
  }
  .box-body{
    font-weight: 700;
    .list{
      letter-spacing: 0;
      line-height: 24px;
      li + li{
        margin-top: 2px;
      }
    }
  }
  .button-by{
    position: absolute;
    bottom: -8px;
    right: -33px;
    width: 100px;
    padding: 10px 10px 20px;
    text-align: center;
    border-top: 1px solid $primary-;
    color: $white;
    font-size: 18px;
    line-height: 18px;
    transform: rotate(-45deg);
    &:hover{
      background: $primary-;
    }
  }
  
  
}
.box-pricing-type-3 + .box-pricing-type-3{
  margin-top: 30px;
}

.box-pricing-white{
  *{
    color: $white;
  }
}

// services
//

.box-services{
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  
  img + *{
    margin-top: 25px;
    @media (min-width: $screen-sm-min) {
      margin-top: 35px;   
    }
  }
  h6 + *{
    margin-top: 20px;
  }
  * + .button-block-right-line{
    margin-top: 20px;
    @media (min-width: $screen-sm-min) {
      margin-top: 30px;   
    }
  }
}

// box-content
//

.box-content-type-1{
  img + *{
    margin-top: 35px;
  }
  h6 + *{
    margin-top: 25px;
  }
  p + *{
    margin-top: 15px;
  }
}

.box-content-type-2{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  flex-direction: column;
  
  @media (min-width: $screen-xs-min){
    padding: 60px;
  }
  
  @media (min-width: $screen-md-min){
    padding: 80px 40px;
    min-height: 620px;
    height: 100%;
  }
  @media (min-width: $screen-lg-min){
    padding: 80px 70px;
    min-height: 670px;
  }
  
  @media (min-width: $screen-xl-min){
    padding: 80px 120px;
  }
  
  img + *{
    margin-top: 40px;
  }
  
  * + .button-block-line{
    margin-top: 40px;
  }
  .subheading-text + *{
    margin-top: 45px;
  }
  p{
    margin-bottom: 0;
    width: 100%;
  }
  .button{
    h2, .big{
      text-transform: none;
    }
    .big{
      letter-spacing: 0;
      font-weight: 400;
    }
  }
}

.box-content-type-3{
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  
  @media (min-width: $screen-sm-min) {
   min-height: 450px;
    height: 100%;
  }
  h2 + *{
    margin-top: 30px;
  }
  .button + *{
    margin-top: 25px;
  }
  h5 + *{
    margin-top: 10px;
  }
  h2{
    color: $gray-lighter-2;
      
  }
  .button{
    font-size: 24px;  
    font-weight: 600;
    letter-spacing: 0;
    text-transform: none;
  }
  h5{
    font-weight: 600;
  }
  &.bg-primary{
    
    p, h2{
      color: rgba($white, .4);
    }
  }
  @media (min-width: $screen-xl-min){
    padding-left: 70px;
    padding-right: 70px;
    p{
      max-width: 400px;
    }
  }
}
.box-content-type-4{
  max-width: 370px;
  text-align: left;
  @media (min-width: $screen-sm-min) {
    margin-left: auto;
    margin-right: auto;
  }
  h2{
    color: $gray;
  }
  h2 + *{
    margin-top: 30px;
  }
  h4 + h5{
    margin-top: 0;
  }
  h5 + p{
    margin-top: 25px;
  }
}

.text-box{
  .big{
    line-height: 24px;
  }
  .big + *{
    margin-top: 25px;
  }
  p{
    line-height: 20px;
  }
}