/*
*
* NAVBAR HOME
* --------------------------------------------------
*/

.rd-navbar.rd-navbar-home-2{

  &.rd-navbar-static{
    .rd-navbar-inner{
      background: $white;
      max-width: 1800px;
      padding-top: 25px;
      padding-bottom: 25px;
    }
    .rd-navbar-aside-right{
      margin-top: 20px;
    }
    .rd-navbar-nav{
      >li + li{
        margin-left: 40px;
      }
      >li{

        &:after{
          position: absolute;
          display: block;
          top: 50%;
          right: -22px;
          content: '';
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: $primary;
          transform: translateY(-50%);
        }
        &:last-child{
          &:after{
            display: none;
          }
        }
        >a{
          font-size: 14px;
          line-height: 20px;
          font-weight: 600;
          color: $primary-;
          text-transform: uppercase;
          letter-spacing: .15em;
          &:hover{
            color: $secondary-;
          }

        }
        &.focus, &.active{
          >a{
            color: $secondary-;
          }
        }
        .rd-navbar-submenu-toggle{
          display: none;
        }
      }
    }
    // navbar stuck
    //
    &.rd-navbar--is-stuck{
      .rd-navbar-inner{
        padding-top: 10px;
        padding-bottom: 10px;
      }
      .rd-navbar-aside-right{
        margin-top: 0;
      }
      .rd-navbar-brand{
        .brand-name{
          width: 145px;
        }
      }
    }
  }
  
  //  navbar fixed
  //  
  &.rd-navbar-fixed{
    
    .rd-navbar-brand{
      width: 100%;
      text-align: center;
      .brand-name{
        display: inline-block;
        transform: translateX(-21px);
      }
    }
    
    .rd-navbar-nav {
      li{
        > a{
          color: $secondary-;
          &:hover{
          color: $white;
        }
        }
        &.active, &.opened{
          > a{
            color: $white;
          }
         
        }
      }
    }
  }
}

