// Mixins

// Clearfix
@mixin clearfix() {
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}

// Disable padding - top on two similar backgrounds
@mixin bg-behaviour($bg-color, $contrast-bg: null, $contrast-context: null) {
  background-color: $bg-color;

  html.contrast & {
    &{
      background-color: $contrast-bg;

      @if($contrast-context != null) {
        @extend #{"%" + $contrast-context};
      }
    }
  }

  & + & {
    padding-top: 0;
  }
}

// Text variant
@mixin text-variant($parent, $color, $hover-color) {
  #{$parent} {
    color: $color;
  }
  a#{$parent}:hover,
  a#{$parent}:focus {
    color: $hover-color;
  }
}

// Reset default button styles
@mixin reset-button {
  background: none;
  border: 0;
  color: inherit;
  cursor: pointer;
  font: inherit;
  overflow: hidden;
  padding: 0;
  position: relative;
  //touch-action: manipulation;
  //user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  outline: none;

  &:active {
    cursor: wait;
  }
}