/*
*
* Lists
* --------------------------------------------------
*/


ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}


.list {
  letter-spacing: .075em;
  > li + li {
    margin-top: 10px;
  }
}


// List inline
//

.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 5px;
    padding-right: 5px;
  }
}


// Description Lists
//

dl {
  margin: 0;
}


// List terms
//

.list-terms {
  margin-top: 35px;
  dt + dd { margin-top: 5px; }
  dd + dt { margin-top: 25px; }
}


// List index
//

.list-index {
  counter-reset: li;

  li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal-leading-zero)". "; 
        counter-increment: li;
        color: $secondary-;
      }
    }
  }
}


// List marked
//

.list-marked {
  li {
    position: relative;
    padding-left: 16px;

    &:before {
      content: '';
      display: inline-block;
      background: $gray-dark;
      width: 5px;
      height: 5px;
      position: absolute;
      left: 0;
      top: 13px;
      border-radius: 50%;
      transform: translateY(-50%);
    }
  }
  &-primary{
    li{
      &:before{
        background: $primary-;
      }
    }
  }
}

// List types
//

.list-primary{
  color: $primary;
  a{
    color: $primary;
  }
}

.list-uppercase{
  text-transform: uppercase;
}
.list-site-map{
  padding-left: 20px;
  li{
    position: relative;
    &:after{
      position: absolute;
      top: 7px;
      left: -20px;
      content: '\f054';
      font-family: 'FontAwesome';
      color: $secondary;
      font-size: 10px;
      line-height: 1;
      opacity: 0;
      transition: all .3s;
    }
    &:hover{
      &:after{
        opacity: 1;        
      }
    }
    a{
      &:hover{
        color: $primary;
        text-decoration: none;
      }
    }
  }
  ul{
    margin-top: 10px;
  }
  & + *{
    margin-top: 40px;
  }
}