/*
*
* Custom Tables
* --------------------------------------------------
*/


.table-custom-wrap{
  overflow-x: auto;
}

.table-custom {
  width: 100%;
  text-align: left;

  tr:first-child {
    td {
      font-weight: 400;
    }
  }
  tr:last-child{
    td{
      color: $secondary-;
    }
  }

  th tbody, td tbody {
    padding: 7px 18px;
  }

  @media (min-width: $screen-md-min) {
    th, td {
      &:first-child {
        padding-left: 56px;
      }
    }
  }
}

.table-custom {
  font-size: 18px;
  text-align: left;

  th {
    color: $gray-darker;
    font-weight: 400;
  }

  td {
    color: $gray-light;
  }

  tbody {
    tr {
      border: 1px solid $gray-lighter;
      border-left: none;
      border-right: none;
      &:first-child {
        border: 1px solid $gray-lighter;
      }
      td, th {
        border: none;
        vertical-align: middle;
        padding: 13px 23px;
        white-space: nowrap;
      }
      th, td {
        padding: 4px 16px;
      }
    }
  }

  &.table-fixed {
    @media (min-width: $screen-md-min) {
      table-layout: fixed;
    }

    th tbody, td tbody {
      padding: 0;
    }

    @media (min-width: $screen-md-min) {
      th, td {
        &:first-child {
          padding-left: 56px;
        }
        padding: 13px 23px;
      }
    }
  }

  &.table-hover-rows,
  &.striped-table,
  &.condensed-table,
  &.bordered-table {
    tbody {
      tr {
        &:first-child {
          border: none;
          border-bottom: 1px solid $gray-darker;
        }
      }
    }
  }

  &.table-hover-rows {
    tbody {
      tr {
        &:nth-child(n+2) {
          &:hover {
            background: $gray-lighter;
            cursor: pointer;
          }
        }
      }
    }
  }

  &.striped-table,
  &.condensed-table,
  &.bordered-table {
    tbody {
      tr {
        &:hover {
          background: none;
          cursor: auto;
        }
        &:nth-child(even) {
          background: rgba(#1f2932, 0.05);
        }
      }
    }
  }

  &.condensed-table {
    @media (min-width: $screen-md-min) {
      th, td {
        padding: 9px 23px;
      }
    }
  }

  &.bordered-table {
    tbody {
      tr {
        border: 1px solid $gray-lighter;
        td, th {
          border: 1px solid $gray-lighter;
        }
        th {
          border-bottom: none;
        }
        &:nth-child(n+2) {
          td {
            border-top: none;
          }
        }
        &:nth-child(even) {
          background: none;
        }

        th, td {
          &:first-child {
            width: 110px;
          }
        }
      }

      @media (min-width: $screen-md-min) {
        th, td {
          &:nth-child(n+2) {
            padding-left: 20px;
          }
          padding: 12px 19px;
        }
      }
    }
  }
  &.table-primary {
    tbody {
      tr {
        border: 1px solid #f9fbfc;
        background: $white;
        &:nth-child(odd) {
          background: $gray-lighter;
        }
        &:first-child {
          th {
            font-weight: 400;
          }
        }
        th {
          background: $primary;
          color: $white;
          border-top-color: $primary;
        }
        

      }
    }
    &-type-2{
      tbody {
        tr {
          border-color: #f9fbfc;
          &:nth-child(odd) {
            background: $white;
          }
        }
      }
    }
  }
}
.table-pricing{
  tr{
    td:first-child{
      color: $secondary-;
      text-align: left;
      white-space: nowrap;
      @media (min-width: $screen-md-min) {
        padding-left: 40px;
      }
      @media (min-width: $screen-lg-min){
        padding-left: 60px;
      }
    }
    th{
      text-align: center;
    }
    td{
      text-align: center;
    }
  }
}
.table-primary-type-1{
  tbody{
    tr{
      th{
        padding: 20px;
        &:first-child{
          @media (min-width: $screen-md-min){
            padding-left: 60px;
          }
        }
      }
      td{
        background: $white;
        color: $gray-light-4;
        white-space: nowrap;
        padding: 12px 20px;

        .button{
          min-width: 145px;
        }
        &:first-child{
          @media (min-width: $screen-md-min){
            padding-left: 60px;
          }
        }
        &:last-child{
          text-align: center;
        }
      }
      &:last-child{
        td{
          color: $gray-light-4;
        }
      }
    }
  }
  
}

.table-custom-type-1{
  tr:last-child{
    td{
      color: $gray-light-4;
    }
  }
}
.table-color-head{
  tbody{
    tr{
      border: 1px solid #f9fbfc;
      th{
        color: $primary-;
        background: $gray-lighter;
       
      }
      td{
        border: none;
      }
    }
  }
}