/*
*
* blockquote
* --------------------------------------------------
*/

.blockquote-default{
  padding: 65px 25px;
  @media (min-width: $screen-xs-min){
    padding: 65px;
  }
  * + .blockquote-img{
    margin-top: 35px;
  }
  .blockquote-img + .quote{
    margin-top: 25px;
  }
  .quote{
    position: relative;
    q{
      display: block;
      position: relative;
      font-size: 18px;
      padding-top: 30px; 
      &:after{
        display: none;
      }
      &:before{
        content: '';
        position: absolute;
        left: 50%;
        top: -43px;
        font-family: $font-family-sec;
        font-size: 90px;
        color: $secondary-;
        transform: translateX(-50%);
      }
    }
    * + .name{
      margin-top: 20px;
    }
    .name{
      color: $secondary-;
      font-weight: 600;
      line-height: 20px;
      cite{
        font-size: 18px;
        font-style: normal;
      }
      span{
        display: block;
      }
    }
  }
}

.blockquote-type-1{
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  
  .quote{
    position: relative;
    q{
      display: block;
      position: relative;
      padding-top: 30px;
      line-height: 20px;

      &:after{
        display: none;
      }
      &:before{
        content: '';
        position: absolute;
        left: 50%;
        top: 10px;
        font-family: $font-family-sec;
        font-size: 60px;
        color: $secondary-;
        transform: translateX(-50%);
      }
    }
  }
  
  .name{
    color: $secondary-;
    font-weight: 600;
    line-height: 20px;
    cite{
      font-size: 18px;
      font-style: normal;
    }
    span{
      display: block;
    }
  }
  
  .quote + *{
    margin-top: 25px;
  }
  .blockquote-img + *{
    margin-top: 25px;
  }
  &.blockquote-carousel{
    max-width: 470px;
    .quote{
      q{
        font-size: 18px;
        line-height: 24px;
      }
    }
  }
}
.blockquote-type-2{
  text-align: left;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
  .quote{
    
    padding-left: 30px;
    q{
      position: relative;
      display: inline-block;
      
      font-size: 24px;
      line-height: 36px;
      font-weight: 600;
      &:after, &:before{
        font-family: $font-family-sec;
        color: $secondary-;
        font-size: 90px;
      }
      &:after{
        position: absolute;
        transform: rotateX(-180deg);
        bottom: 25px;
      }
      &:before{
        position: absolute;
        left: -20px;
        top: -5px;
        transform: translateX(-50%);
      }
      @media (min-width: $screen-md-min) {
        font-size: 36px;
        line-height: 48px;
      }
    }
  }
  * + .name{
    margin-top: 30px;
  }
  .name{
    color: $secondary-;
    font-weight: 600;
    line-height: 20px;
    cite{
      font-size: 24px;
      font-style: normal;
    }
    span{
      font-size: 18px;
      display: block;
    }
  }
}