//== Breadcrumbs variables
//
//##

$bread-color: $white;
$bread-active-color: $white;


.breadcrumb-wrapper{
  padding-top: 40px;
  padding-bottom: 40px;
  @media (min-width: $screen-md-min){
    padding-top: 120px;
    padding-bottom: 120px;
  }
}

.breadcrumb-custom{
  margin-top: 10px;
  list-style: none;

  > li {
    display: inline-block;
    color: $bread-active-color;
    font-weight: 600;
    letter-spacing: .2em;
    text-transform: uppercase;
    font-size: 18px;

    > a{
      color: $bread-color;

      &:hover{
        text-decoration: underline;
      }
    }

    &:after {
      padding: 0 5px 0 8px;
      
      content: "/";
      color: $white;
    }
    &:last-child{
      &:after{
        display: none;
      }
    }
  }
}