/*
*
* Posts
* --------------------------------------------------
*/


.post-blog{

  &__title{

  }
}

.post-type-1{
  max-width: 370px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  .img-block + .post-body{
    margin-top: 35px;
  }
  
  .date{
    color: $secondary-;
    font-weight: 600;
  }
  * + .date{
    margin-top: 0;
  }
}
.post-type-2 + .post-type-2{
  margin-top: 50px;
}

.post-type-2{
  text-align: left;

  p{
    line-height: 20px;
  }
  
  .date-block{
    text-align: center;
    display: inline-block;
    width: 70px;
    padding: 10px;
    background: transparent;
    border: 1px solid $gray-light-5;
    span{
      text-transform: uppercase;
      letter-spacing: .075em;
      font-weight: 700;
    }
    h3{
      color: $gray;
      line-height: 1;
    }
   
  }
  @media (min-width: $screen-md-min){
    .unit.unit-xs-horizontal{
      >.unit__left + .unit__body{
        padding-left: 30px;
      }
    }
  } 
  * + img{
    margin-top: 25px;
  }
  h5 + *{
    margin-top: 10px;
  }
}