/*
*
* Offsets
* --------------------------------------------------
*/

// Element offset
* + p { margin-top: 15px; }
p + p { margin-top: 12px; }


// Headings
h1 + h2 { margin-top: 32px; }
h2 + h3 { margin-top: 32px; }
h3 + h4 { margin-top: 32px; }
h4 + h5 { margin-top: 40px; }
h5 + h6 { margin-top: 40px; }
h1 + p { margin-top: 22px; }


// Headings + Paragraph
h2 + p { margin-top: 12px; }
h3 + p { margin-top: 12px; }
h4 + p { margin-top: 30px; }
h5 + p { margin-top: 12px; }
h6 + p { margin-top: 12px; }


// Paragraph + Headings
p + h2 { margin-top: 45px; }
p + h3 { margin-top: 45px; }
p + h4 { margin-top: 45px; }
p + h5 { margin-top: 45px; }
p + h6 { margin-top: 45px; }


// Tags + Tags
img + p { margin-top: 25px; }
h3 + img { margin-top: 42px; }
h4 + img{ margin-top: 30px;}

dt + dd{
  margin-top: 5px;
}
dd + dt{
  margin-top: 40px;
}


// Tags + Classes
p + .list-marked { margin-top: 10px; }
h2 + .big{ margin-top: 45px;}


// Classes
* + .rd-mailform { margin-top: 30px; }
* + .button-block-right-line{  margin-top: 30px;}
.big+ .big{ margin-top: 20px;}


// Classes + Tags
.range + h3 { margin-top: 80px; }


// Classes + Classes
.shell + .shell { margin-top: 60px; }
.range + .range { margin-top: 60px; }
* + .list-index{  margin-top: 20px;}





// Media offsets

@media (min-width: $screen-sm-min) {

}

@media (min-width: $screen-md-min) {

}

@media (min-width: $screen-lg-min) {

}

html {
  // Range spacing
  .range-60 {
    @include responsive-offset(60px);
  }
  .range-50 {
    @include responsive-offset(50px);
  }

  .range-40 {
    @include responsive-offset(40px);
  }
  
  .range-30 {
    @include responsive-offset(30px);
  }

  .range-20 {
    @include responsive-offset(20px);
  }
  
  .range-15 {
    @include responsive-offset(15px);
  }
  .range-10 {
    @include responsive-offset(10px);
  }
  .range-5 {
    @include responsive-offset(5px);
  }
  .range-0 {
    @include responsive-offset(0px);
  }

  @media (min-width: $screen-md-min) {
    .range-md-30 {
      @include responsive-offset-media(30px);
    }
  } 
  @media (min-width: $screen-lg-min) {
    .range-lg-90 {
      @include responsive-offset-media(90px);
    }
    .range-lg-0 {
      @include responsive-offset-media(0);
    }
  }
}

.range-form-offset{
  margin-left: -5px;
  margin-right: -5px;
  >[class*="cell-"]{
    padding-left: 5px;
    padding-right: 5px;
  }
}

.instafeed{
  .range{
    margin-left: -3px;
    margin-right: -3px;
    >[class*="cell-"]{
      padding-left: 3px;
      padding-right: 3px;
    }
    .cell-xxs-6{
      @media (max-width: $screen-xs-min - 1px){
        flex-basis: 50%;
        max-width: 50%;
      }
    }
  }
}