/*
*
* RD Calendar
* --------------------------------------------------
*/


.rd-calendar {
  position: relative;

  .rdc-table-header,
  .rdc-table-row{
    display: flex;
  }

  .rdc-table_day,
  .rdc-table_date-outer{
    width: 14.28%;
    flex-grow: 1;
  }

  .rdc-table_event{

    border-radius: 0;
    cursor: pointer;
  }

  .rdc-table-row{
    margin-top: 0;
    .rdc-table_date-outer{
      position: relative;
      border-left: 1px solid $gray-light-3;
      background: $white;
      height: 60px;
      @media (min-width: $screen-sm-min) {
        height: 80px;   
      }
      @media (min-width: $screen-md-min){
        height: 120px;
      }
      &:last-child{
        border-right: 1px solid $gray-light-3;
      }
      .rdc-table_date{
        background: #e9e9e9;
        padding: 5px ;
        text-align: center;
        height: 30px;
        line-height: 1;
        color: $gray;
        font-size: 16px;
        @media (min-width: $screen-sm-min) {
          text-align: left;
          font-size: 18px;
          padding: 5px 10px;
        }
        
        &.rdc-table_prev, &.rdc-table_next{
          background-color: $gray-lighter;
        }
        
        &.rdc-table_today{
          background: $primary-;
          color: $white;
        }
      }
    }
    
    &:last-child{
      .rdc-table_date-outer{
        border-bottom: 1px solid $gray-light-3 ;
      }
    }
  }

  .rdc-calendar-event-panel,
  .rdc-table_events{
    overflow: hidden;
   
  }

  .rdc-calendar-event{
    margin-bottom: 0;
  }

  .rdc-table_date-outer + .rdc-table_date-outer{
    margin-left: 0px;
    
  }

  &.show-events {

  }
  
  .rdc-table-header{
    background: $primary-;
    .rdc-table_day{
      color: $white;

      text-transform: capitalize;
      padding: 20px 5px;
      @media (min-width: $screen-sm-min) {
        padding: 20px 10px;   
      }
     
    }
  }
}


// Today
.rdc-today_day{

}

.rdc-today_date{

}

.rdc-today_month{

}

.rdc-today_fullyear{

}


// Panel
.rdc-panel{
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  h5{
    display: flex;
    >* + *{
      margin-left: 10px;
    }
  }
}

// Controls
.rdc-next, .rdc-prev, .rdc-events_close{
  color: $gray;
  font: 400 18px/38px "FontAwesome";
  cursor: pointer;
  transition: .3s all ease;
  opacity: .5;

  &:hover {
    opacity: 1;
  }
}

.rdc-next{
  &:before {
    content: '\f178';
  }
}

.rdc-prev{
  &:before {
    content: '\f177';
  }
}

.rdc-events_close{
  &:before {
    content: '\f00d';
    color: $primary-;
  }
}

// Events
.rdc-events{
  opacity: 0;
  visibility: hidden;
  transition: .4s all ease-in-out;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: $white;
  padding: 10px 20px 20px;
  box-shadow: 0 0 2px 0 rgba($secondary-, .5);

  .rd-calendar.show-events & {
    opacity: 1;
    visibility: visible;
  }
}



.rdc-event{
  display: none;

  &.active {
    display: block;
  }
}

// Table
.rdc-table{
  position: relative;
  width: 100%;
  table {
    border-spacing: 4px;
    border-collapse: separate;
    margin-left: auto;
    margin-right: auto;
  }

  td {
    position: relative;
  }
}

.rdc-month{

}

.rdc-fullyear{

}

.rdc-table_day{
  color: $gray;
  padding: 5px 0;
  text-transform: uppercase;
  font-size: 14px;
  @media (min-width: $screen-sm-min) {
    font-size: 18px;
  }
}

.rdc-table_date{
  //width: 60px;
  height: 60px;
  font-weight: 300;
  font-size: 20px;
  line-height: 54px;
  padding: 3px;
  color: $white;
  text-align: center;
  background-color: $gray;
  box-sizing: border-box;
}

.rdc-table_today{
  background-color: $gray-light;
}

.rdc-table_event{
  cursor: pointer;
  transition: .3s all ease;
  font-size: 0;
  position: relative;
  height: 30px;
  @media (min-width: $screen-sm-min) {
    height: 50px;   
  }
  @media (min-width: $screen-md-min){
    height: 90px;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    padding: 20px 10px 15px;
  }
  
  p{
    margin: 0;
  }
  
  
  &:before{
    position: absolute;
    top: 50%;
    left: 50%;
    content: '\f00c';
    font-family:'FontAwesome';
    color: $primary-;
    font-size: 14px;
    transform: translate(-50%, -50%);
    @media (min-width: $screen-md-min){
      display: none;
    }
  }
   
  &:hover{
    color: #fff;
    background-color: $primary-;
    &:before{
      color: $white;
    }
  }
  
}
.rdc-table_events-count{
  display: none;
}


.rd-calendar[data-events-inline="true"]{
  .rdc-table_date-outer{
    .rdc-table_events{
      display: none;
    }
  }

  .rdc-table_events-count,
  .rdc-events{
    display: none;
  }
}