/*
*
* NAVBAR HOME
* --------------------------------------------------
*/

.rd-navbar.rd-navbar-home-3{
  background: $white;

  &.rd-navbar-fullwidth{
    .rd-navbar-inner{
      max-width: 100%;
      padding: 0;
      background: $white;
      
      .rd-navbar-panel{
        padding: 10px 15px 30px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        max-width: 1200px;
        width: 100%;
        
        margin-left: auto;
        margin-right: auto;
        
        .rd-navbar-top-panel{
          margin-left: 160px;
          .contact-info{
            p{
              > a{
                color: $secondary-;
                &:hover{
                  color: $primary;
                }
              }
            }
            .link-block.link-block-primary{
              a{
                &:hover{
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
      
      .rd-navbar-aside-right{
        background: $gray-lighter;
        padding-top: 0;
        padding-bottom: 0;
        margin-top: 0;
        .rd-navbar-nav-wrap{
          max-width: 1200px;
          width: 100%;
          padding-left: 15px;
          padding-right: 15px;
        }
      }
    }
    .rd-navbar-top-panel{
      background: $white;
      text-align: left;
      padding: 0;
      .unit__left{
        p{
          margin-left: 10px;
          display: inline-block;
          color: $gray;
        }
      }
      
      .unit__body{
        color: $secondary-;
        .link-block{
          a{
            color: $secondary-;
            &:hover{
              color: $primary-;
              &:after{
                color: $secondary-;
              }
            }
            &:after{
              display: inline-block;
              content: ',';
              
            }
            &:last-child{
              &:after{
              
                display: none;
              }
            }
          }
          a + a{
            margin-left: 4px;
          }
          &-primary{
            a{
              color: $primary-;
            }
          }
        }
      }
      .unit__left + .unit__body{
        margin-top: 5px;
        padding-top: 0;
      }
    }
    .rd-navbar-nav{
      text-align: left;
      >li + li{
        margin-left: -4px;
      }
      >li{
        
        >a{
          position: relative;
          font-size: 14px;
          line-height: 20px;
          padding: 11px 25px 12px;
          color: $primary-;
          font-weight: 600;
          text-transform: uppercase;
          letter-spacing: .1em;
          &:hover{
            background: $white;
            color: $gray-darker-2;
          }
          &:after{
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            content: '';
            background: $white;
            width: 1px;
            height: 100%;
          }
         
        }
        &:last-child{
          a{
            &:after{
              display: none;
            }
          }
        }
        &.focus, &.active{
          >a{
            background: $white;
            color: $gray-darker-2;
          }
        }
        .rd-navbar-submenu-toggle{
          display: none;
        }
      }
    }

    // navbar stuck
    //
    &.rd-navbar--is-stuck{
      .rd-navbar-inner{
        padding: 0;
        .rd-navbar-panel{
          display: none;
        }
      }
    }
  }
  //  navbar fixed
  //  
  &.rd-navbar-fixed{
    
    .rd-navbar-brand{
      width: 100%;
      text-align: center;
      .brand-name{
        display: inline-block;
        transform: translateX(-21px);
      }
    }
    .rd-navbar-top-panel{
      background: $white;
      text-align: left;
      padding: 20px;
      .unit__left{
        p{
          margin-left: 10px;
          display: inline-block;
          color: $gray;
        }
      }

      .unit__body{
        color: $secondary-;
        .link-block{
          a{
            display: inline-block;
            color: $secondary-;
            &:hover{
              color: $primary-;
              &:after{
                color: $secondary-;
              }
            }
            &:after{
              display: inline-block;
              content: ',';

            }
            &:last-child{
              &:after{

                display: none;
              }
            }
          }
         
          &-primary{
            a{
              color: $primary-;
            }
          }
        }
      }
      .unit__left + .unit__body{
        margin-top: 5px;
        padding-top: 0;
      }
    }
    .rd-navbar-nav {
      li{
        > a{
          color: $secondary-;
          &:hover{
            color: $white;
          }
        }
        &.active, &.opened{
          > a{
            color: $white;
          }

        }
      }
    }
  }
}

