/*
*
* Thumbnails
* --------------------------------------------------
*/


.thumbnail-type-1{
  max-width: 170px;
  margin-left: auto;
  margin-right: auto;
  .caption{
    font-weight: 600;
    color: $secondary-;
    a{
      font-size: 18px;
      color: $secondary-;
      transition: all .3s;
      &:hover{
        color: $primary-;
      }
    }
    p{
      margin-top: 0;
    }
  }
  * + .caption{
    margin-top: 30px;
  }
  .img-block{
    overflow: hidden;
    img{
      transition: all .3s;
      &:hover{
        transform: scale(1.01);
      }
    }
  }

}


// Thumb
//

.thumb{
  position: relative;
  display: inline-block;

  // Bg overlay
  &:before{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    background-color: rgba($primary, .4);
    z-index: 1;
    content: '';
    will-change: opacity;
  }

  // Icon
  &:after{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    color: $white;
    font-family: FontAwesome;
    font-size: 34px;

    content: "\f065";
    z-index: 1;
    will-change: transform, opacity;
  }

  &:before,
  &:after{
    transition: .3s ease;
    opacity: 0;
  }

  &:hover{
    &:before,
    &:after{
      opacity: 1;
    }
  }
}

.thumbnail-instafeed{
  position: relative;
  
  .caption{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba($primary-, .5);
    opacity: 0;
    transition: all .3s;
    ul.list-inline{
      position: absolute;
      top: 50%;
      left: 50%;
      color: $white;
      text-align: center;
      transform: translate(-50%, -50%);
      >li{
        padding-left: 1px;
        padding-right: 1px;
      }
    }
  }
  &:hover{
    .caption{
      opacity: 1;
    }
  }
}