/*
*
* Form styles
* --------------------------------------------------
*/

//== Forms variables
//

$input-height: 50px;

$form-wrap-offset: 15px;

$textarea-default-height:       135px;
$textarea-default-min-height:   $textarea-default-height * 0.8;
$textarea-default-max-height:   $textarea-default-height * 1.7;

$input-color: $gray-light-4;
$input-color-placeholder: $input-color;

$input-bg: $white;

$input-border: $gray-lighter;
$input-border-radius: 0;

$input-font-size: 14px;
$input-line-height: 22px;
$input-font-weight: 400;

$input-padding-vertical: 12px;
$input-padding-horizontal: 15px;

$error-color: #d9534f;
$success-color: #5cb85c;



.rd-mailform {
  position: relative;
  text-align: left;
}

html .rd-mailform-inline{
  text-align: center;
  .button{
    display: inline-block;
  }
  
  @media (min-width: $screen-xs-min){
    @include display-flex();
    
    .form-wrap{
      @include flex-grow(1);
    }
    .form-wrap + .form-wrap{
      margin-top: 0;
    }
    > * + *{
      margin-left: 10px;
    }
    .button{
      margin-top: 0;
      flex: none;
    }
  }
  
  

  
}

.recaptcha{
  transform: scale(0.9);
  transform-origin: left center;
}


// Form input
// -------------------------

.form-input {
  display: block;
  width: 100%;
  height: $input-height;
  padding: $input-padding-vertical $input-padding-horizontal;
  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-bg;
  background-image: none;
  border: 1px solid $input-border;
  border-radius: $input-border-radius;
  -webkit-appearance: none;

  &:focus {
    outline: 0;
  }

  &:-webkit-autofill ~ label,
  &:-webkit-autofill ~ .form-validation {
    color: $gray-base !important;
  }
}

.form-input-type-2{
  border-color: $primary-;
  background: transparent;
}

textarea.form-input {
  height: $textarea-default-height;
  min-height: $textarea-default-min-height;
  max-height: $textarea-default-max-height;
  resize: vertical;
}

// Form wrapper
// -------------------------

.form-wrap {
  position: relative;
}

.form-wrap + .form-wrap{
  margin-top: $form-wrap-offset;
}

* + .form-button,
.form-wrap + .button{
  margin-top: 30px;
}


// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: $input-height / 2;
  left: $input-padding-horizontal;

  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color-placeholder;
  pointer-events: none;
  z-index: 9;
  transition: .3s;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $input-color;
  }
  &.form-label-centered{
    width: 100%;
    text-align: center;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}


.form-label-outside {
  @media (min-width: $screen-sm-min) {
    position: static;
    &, &.focus, &.auto-fill {
      opacity: 1;
      transform: none;
      color: $input-color-placeholder;
      font-size: $input-font-size;
    }

    & + .form-input + .form-validation{
      top: 32px;
    }
  }
}


// Form validation
// -------------------------

.form-validation {
  position: absolute;
  right: 10px;
  top: 3px;
  font-size: 11px;
  line-height: 11px;
  color: $error-color;
  margin-top: 3px;
  transition: .3s;
  z-index: 11;
}

.form-validation-left {
  .form-validation {
    right: auto;
    top: 100%;
    left: 0;
  }
}


// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 0;
  visibility: hidden;
  transform: translateY(calc(100% + 30px));
  transition: .3s all ease;
  z-index: 9999999;
  margin: 15px;

  &.active {
    transform: translateY(0);
    visibility: visible;
  }

  @media (min-width: $screen-sm) {
    margin: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &.error {
    color: $error-color;
  }

  &.success {
    color: $success-color;
  }
}


// Radio and Checkbox Custom
// -------------------------


// Base Styles
//

.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 14px;
    height: 14px;
    margin-left: -20px;
    margin-top: 5px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: $gray-lighter;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.15);

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}


// Custom Radio Styles
//

.radio,
.radio-inline {
  .radio-custom-dummy {
    border-radius: 50%;

    &:after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $gray-darker;
      border-radius: 50%;
    }
  }
}


// Custom Checkbox Styles
//

.checkbox,
.checkbox-inline {
  padding-left: 20px;
  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: 3px;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\f222';
      font-family: "Material Design Icons";
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      color: $gray-darker;
    }
  }
}



//== Toggles Custom
//
//##

$rd-toggles-mftoggle-width:                 32px;
$rd-toggles-mftoggle-height:                14px;
$rd-toggles-mftoggle-background:            $gray-light;
$rd-toggles-mftoggle-border-radius:         15px;
$rd-toggles-mftoggle-offset-left:           6px;
$rd-toggles-mftoggle-offset-right:          20px;
$rd-toggles-mftoggle-shadow:                inset 1px 1px 1px 0 rgba(0, 0, 0, 0.15);
$rd-toggles-mftoggle-active-background:     $primary;
//** after
$rd-toggles-mftoggle-after-width:           20px;
$rd-toggles-mftoggle-after-height:          21px;
$rd-toggles-mftoggle-after-background:      $white;
$rd-toggles-mftoggle-after-border-radius:   20px;
$rd-toggles-mftoggle-after-shadow:          1px 1px 3px 0 rgba(0, 0, 0, 0.25);
//** after active
$rd-toggles-mftoggle-active-after-shadow:   0 1px 3px 1px rgba(0, 0, 0, 0.4), 0 0 0 15px rgba($primary, 0.3);


// Toggles Custom
// -------------------------

.toggle-custom {
  display: none;

  &-dummy {
    content: "";
    display: inline-block;
    width: $rd-toggles-mftoggle-width;
    height: $rd-toggles-mftoggle-height;
    background-color: $rd-toggles-mftoggle-background;
    border-radius: $rd-toggles-mftoggle-border-radius;
    transition: background 0.3s ease;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: $rd-toggles-mftoggle-shadow;

    &:after {
      content: "";
      display: inline-block;
      width: $rd-toggles-mftoggle-after-width;
      height: $rd-toggles-mftoggle-after-height;
      background-color: $rd-toggles-mftoggle-after-background;
      border-radius: $rd-toggles-mftoggle-after-border-radius;
      position: relative;
      box-shadow: $rd-toggles-mftoggle-after-shadow;
      left: -9px;
      top: -4px;
      transition: left 0.3s ease, background 0.3s ease, box-shadow 0.1s ease;
    }
  }
}

.toggle-custom:checked + .toggle-custom-dummy {
  background-color: $rd-toggles-mftoggle-active-background;

  &:after { left: 19px; }
}

// Toggle inline
//

.toggle-inline {
  cursor: pointer;

  .toggle-custom-dummy {
    margin-right: $rd-toggles-mftoggle-offset-right;
    margin-left: $rd-toggles-mftoggle-offset-left;
  }
}

.toggle-inline + .toggle-inline {margin-left: 20px;}


.form-block-img{
  padding: 0 10px;
  max-width: 470px;
  margin-left: auto;
  margin-right: auto;
  color: $white;
  @media (min-width: $screen-sm-min) {
    padding: 0 20px;   
  }
  h2{
    color: $white;
  }
  h2 + .big{
    margin-top: 30px;
  }
  .rd-mailform{
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
  .form-input{
    color: $white;
  }
  .form-label{
    color: $white;
  }
  .form-validation{
    top: 0;
  }
}
.rd-mailform-type-2{
  .range{
    margin-left: -5px;
    margin-right: -5px;
  }
  [class*="cell-"]{
    padding-left: 5px;
    padding-right: 5px;
  }
  @media (min-width: $screen-md-min){
    textarea{
      height: 360px;
    }
  }
  .button{
    display: block;
    width: 100%;
  }
}
.rd-mailform-type-3{
  .icon{
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  textarea{
    height: 50px;
    min-height: inherit;
  }
  .button{
    display: block;
    width: 100%;
  }
}
