/*
*
* Contexts
* --------------------------------------------------
*/


%context-dark {
  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6, & {
    color: $white-;
  }
}

%context-light {
  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6 {
    color: $headings-color;
  }

  & {
    color: $text-color-;
  }
}

.context-dark {
  @extend %context-dark;
}

.context-light {
  @extend %context-light;
}

// Contrast variant of site
html.contrast{

  .text-gray{
    color: $white-;
  }
  
 
  .bg-default{
    .list-primary{
      li{
        color: $white;
      }
      
    }
    .text-primary{
      color: $secondary-;
    }
    .button-primary {
      @include btn-variant($secondary-, transparent, $secondary-, $white, $secondary-, $secondary-);

      &:before, &:after{
        background: $secondary-;
      }

      &:hover{
        &:before, &:after{
          background: $white;
        }
      }
    }

    .button-block-right-line{
      &:after{
        border-top: 1px solid $secondary-;
      }
    }
    .button-block-line{
      &:after, &:before{
        border-top: 1px solid $secondary-;
      }
    }
    .bg-secondary{
      .button-primary {
        @include btn-variant($primary-, transparent, $primary-, $white, $primary-, $primary-);

        &:before, &:after{
          background: $primary-;
        }
        
        &:hover{
          &:before, &:after{
            background: $white;
          }
        }
      }
    }
    
    .subheading-text-primary{
      color: $secondary-;
    }
    
    .date-block{
      color: $secondary-;
      h3{
        color: $secondary-;
      }
    }
    h5, h6{
      a{
        color: $secondary-;
        &:hover{
          text-decoration: underline;
        }
      }
    }
    .box-pricing-type-1{
      border: 1px solid $secondary-;
      .box-header{
        border-bottom: 1px solid $secondary-;
      }
    }
    .box-team-type-1-modern{
      color: $gray-;
    }
    .rd-calendar{
      .rdc-next, .rdc-prev{
        color: $white-;
      }
      .rdc-table_event, .rdc-event{
        color: $gray-;
      }
    }
  }
  
  
  // Footer
  // ===
  

  hr{
    border-color: $gray-dark;
  }

  // RD Navbar
  // ===
  .rd-navbar-static .rd-navbar-top-panel, .rd-navbar-fullwidth .rd-navbar-top-panel{
    background-color: $white;
    color: $text-color;
  }

  .rd-navbar-static .rd-navbar-top-panel, .rd-navbar-static .rd-navbar-top-panel a, .rd-navbar-fullwidth .rd-navbar-top-panel, .rd-navbar-fullwidth .rd-navbar-top-panel a{
    color: $text-color;
  }

  

  
}