/*
*
* Sections
* --------------------------------------------------
*/


// Section Spacing
// -------------------------

.section-xxs{
  padding-top: 20px;
  padding-bottom: 20px;
}

.section-xxs-type-1{
  padding-top: 30px;
  padding-bottom: 30px;
}

.section-sm {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section-md {

  padding-top: 60px;
  padding-bottom: 60px;
  
  @media (min-width: $screen-md-min){
    padding-top: 90px;
    padding-bottom: 90px;
  }
  
}

.section-lg {

  padding-top: 60px;
  padding-bottom: 60px;
  
  @media (min-width: $screen-md-min) {
    padding-top: 120px;
    padding-bottom: 120px;
  }
  
  
}

// Section Styles
// -------------------------


.shell-wide.shell-wide-type-1{
  width: 100%;
  padding: 0;
  @media (min-width: $screen-xs-min){
    padding-left: 15px;
    padding-right: 15px;
    max-width: 1800px;
  }
}

.section-relative {
  position: relative;
  z-index: 1;
}

.section-screen{
  min-height: calc(100vh - 600px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h1 + h2{
    margin-top: 0;
  }
  h2 + *,  * + h5{
    margin-top: 40px;
  }
  
  h5 + *{
    margin-top: 25px;
  }
  * + .group-sm{
    margin-top: 35px;
  }
  .button{
    font-size: 18px;
    font-weight: 400;
  }
}

// Section Grid Demonstration
.section-grid-demonstration {
  @media (max-width: $screen-xs-min) {
    [class^="col"] {
      padding: 5px;
    }
  }
  .grid-demonstration-item {
    background: $gray-lighter;

    @media (min-width: $screen-sm-min) {
      padding: 35px 30px;
      text-align: left;
    }

    @media (max-width: $screen-md-max) {
      h3 {
        font-size: 16px;
        line-height: 16px;
      }
      p {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
      }
    }
  }

  .divider + .row {
    margin-top: 35px;
  }
}
.section-two-column{
  overflow: hidden;
  .block-left{
    overflow: hidden;
    width: 100%;
  }
  @media (min-width: $screen-lg-min){
    display: flex;
    > .block-center{

      width: 100%;
      max-width: 770px;
    }
    .block-left{
      position: relative;
      max-width: 39%;
      width: 100%;
      .img-fixed{
        position: absolute;
        img{
          
          min-height: 850px;
        }
      }
    }

  }
  @media (min-width: $screen-xl-min){
    .block-left{
      .img-fixed{
        img{
          width: 100%;
        }
      }
    }
  }
}
.section-three-column{
  overflow: hidden;
  .block-left, .block-right{
    width: 100%;
    overflow: hidden;
  }
  @media (min-width: $screen-lg-min){
    display: flex;
      > .block-center{
        
        width: 100%;
        max-width: 770px;
      }
      .block-left, .block-right{
        position: relative;
        max-width: calc(50% - 385px);
        width: 100%;
        .img-fixed{
          position: absolute;
          
        }
      }
 
  }
  @media (min-width: $screen-xl-min){
    .block-left, .block-right{
      .img-fixed{
        img{
          width: 100%;
        }
      }
    }
  }
}

.section-counter{
  .counter-text{
    padding-left: 10px;
    padding-right: 10px;
  }
}

.section-grid-demonstration{
  h4 + *{
    margin-top: 30px;
  }
}