// Import variables
@import "variables-custom";

// Import mixins
@import "mixins";


/*
*
* Main Styles
* ==================================================
*/

// Remove leading spacing of element
html {
  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

html.boxed{
  body{
    background: linear-gradient(45deg, #151515 12%, transparent 0, transparent 88%, #151515 0),
    linear-gradient(135deg, transparent 37%, #181818 0, #181818 63%, transparent 0),
    linear-gradient(45deg, transparent 37%, #151515 0, #151515 63%, transparent 0) #131313;
    background-size: 35px 35px;
  }

  .page{
    max-width: 1600px;
    margin-left: auto;
    margin-right: auto;
  }

  .rd-navbar.rd-navbar--is-stuck{
    max-width: 1600px;
  }

  &.contrast{
    body{
      background: linear-gradient(45deg, #f3f3f3 12%, transparent 0, transparent 88%, #f3f3f3 0),
      linear-gradient(135deg, transparent 37%, #f1f1f1 0, #f1f1f1 63%, transparent 0),
      linear-gradient(45deg, transparent 37%, #f3f3f3 0, #f3f3f3 63%, transparent 0) #f9f9f9;
      background-size: 35px 35px;
    }
  }
}


// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $text-color-;
  background-color: $body-bg;
}


// Links

a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover{
  color: $link-hover-color;
}

.link-press{
  color: $link-press-color;
}

.p a:hover,
.list a:hover{
  text-decoration: underline;
}


// Images

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}


/*
*
* Typography
* ==================================================
*/

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  color: $headings-color-;
  letter-spacing: .025em;

  a {
    transition: .3s all ease;
  }

  a:hover {
    color: darken($primary, 15%);
  }
}

h1,
.heading-1 {
  font-size: 36px;
  line-height: 1.2;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h1;
    font-size: $font-size-h1;
  }
}

h2,
.heading-2 {
  font-size: 32px;
  line-height: 1.2;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
}

h3,
.heading-3 {
  font-size: 28px;
  line-height: 1.2;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.heading-4 {
  font-size: 24px;
  line-height: 1.2;
  font-family: $font-family-base;
  font-weight: 700;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.heading-5 {
  font-size: 20px;
  line-height: 1.2;
  font-family: $font-family-base;
  font-weight: 700;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.heading-6 {
  font-size: 17px;
  line-height: 1.2;
  font-family: $font-family-base;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .1em;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}


// Big text
.big {
  font-size: $font-size-large;
  line-height: 24px;
}


// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}


// Divider
// -------------------------

hr{
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $gray-lighter;
}

.divider {
  margin-top: 0;
  margin-bottom: 0;
  border: none;

  height: 2px;
  width: 60px;
  background-color: $gray-base;
}

mark,.mark{
  background: $primary;
  color: $white;
}

/*
*
* Backgrounds
* ==================================================
*/
// bg-behaviour() off padding top if two section with same bg that are beside
// bg-behaviour($bg-color, $contrast-bg: null, $contrast-context: null)

.bg-default {
  @include bg-behaviour($body-bg-, $text-color-, "context-dark");
}

.bg-gray-base {
  @extend %context-dark;

  @include bg-behaviour($gray-base-, $white-, "context-light");
}

.bg-white {
  @include bg-behaviour($white);
}

.bg-gray-darker {
  @include bg-behaviour($gray-darker);
}

.bg-secondary {
  @include bg-behaviour($secondary);
}

.bg-gray-lighter{
  @include bg-behaviour($gray-lighter, $gray-lighter, "context-light" );
}

.bg-primary {
  @include bg-behaviour($primary);

  // Change color of primary button on primary bg
  .btn-primary {
    @include btn-variant($primary, $white, $white, $primary, darken($white, 5%), darken($white, 5%));
  }
}

.bg-image {
  background-size: cover;
  background-position: center center;
}

.dark-filter{
  position: relative; 
  > *{
    position: relative;
    z-index: 1;
  }
  &:after{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    content: '';
    background: rgba(0,0,0,.4);
    z-index: 0;
  }
}
.white-filter{
  position: relative;
  > *{
    position: relative;
    z-index: 1;
  }
  &:after{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    content: '';
    background: rgba($white,.9);
    z-index: 0;
    @media (min-width: $screen-md-min){
      display: none;
    }
  }
  
}

html.desktop {
  .bg-fixed {
    @media (min-width: $screen-md-min) {
      background-attachment: fixed;
    }
  }
}

.img-fixed{
  position: relative;
  min-height: 100%;


  img{
    width: 100%;
    max-width: none;

  }
  
  @media (min-width: $screen-lg-min){
   
    max-width: 100%;
    width: 100%;
    min-height: 100%;
    height: auto;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    img{
      min-width: 992px;
      min-height: 810px;
      transform: translateX(-45%);
    }
  }
  @media (min-width: $screen-xl-min){
    img{
     
      transform: translateX(-20%);
    }
  }
  @media (min-width: 2300px){
    img{

      transform: translateX(0);
    }
  }
  @media (min-width: 1920px){
    min-width: 100%;
    
  }
 
  &-right{
    @media (min-width: $screen-lg-min){
      left: inherit;
      right: 0;
      width: inherit;
    }
    @media (min-width: 1920px){
      min-width: 100%;
    }
  }
}
.img-fixed-type-2{
  position: absolute;
  bottom: 0;
  right: -50px;
  width: 55%;
}
.img-hover{
  transition: all .3s;
  &:hover{
    transform: scale(1.01);
  }
}

/*
*
* Colors
* ==================================================
*/

.page {
  @include text-variant('.text-gray-base', $gray-base, $primary);
  @include text-variant('.text-gray-darker', $gray-darker, $primary);
  @include text-variant('.text-gray-dark', $gray-dark, $primary);
  @include text-variant('.text-gray', $gray, $primary);
  @include text-variant('.text-secondary', $secondary-, $primary);
  @include text-variant('.text-gray-light', $gray-light, $primary);
  @include text-variant('.text-gray-lighter', $gray-lighter, $primary);
  @include text-variant('.text-primary', $primary, darken($primary, 10%));
  @include text-variant('.text-white', $white, darken($white, 10%));
}


/*
*
* Main layout
* ==================================================
*/

// Page
//

.page {
  position: relative;
  
  overflow: hidden;
  min-height: 100vh!important;
}


// Page header
//

.page-header {
  //Kill bootstrap style
  padding: 0;
  margin: 0;
  border-bottom: none;
}


// Page footer
//

.page-footer {
  padding-top: 40px;
  padding-bottom: 30px;
  background-color: #f3f3f3;
}

.page-footer-default{
  .icon{
    line-height: 22px;
  }
  .big{
    font-weight: 600;
    color: $gray-light;
    margin-bottom: 0;
    line-height: 22px;
  }
  .big + *{
    margin-top: 5px;
  }
  address{
    color: $white;
    p{
      font-weight: 600;
      a{
        color: $white;
        &:hover{
          text-decoration: underline;
        }
      }
    }
    
  }
  .link-block{
    a{
      color: $white;
      display: inline-block;
      margin-right: 3px;
      font-weight: 600;
      
      &:hover{
        color: $primary;
        &:after{
          color: $white;
        }
      }
      &:after{
        content: ',';
      }
      &:last-child{
        &:after{
          display: none;
        }
      }
    }
    
    &.link-block-primary{
      a{
        color: $primary;
        font-weight: 400;
        &:hover{
          color: $white;
        }
      }
      
    }
  }
  .box-privacy{
    color: $gray-light;
    a{
      color: $gray-light;
      &:hover{
        text-decoration: underline;
      }
    }
  }
  &.page-footer-2{
    address{
      color: $secondary-;
      p{
        font-weight: 400;
        a{
          color: $secondary-;
        }
      }
    }
    .link-block{
      a{
        font-size: 18px; 
        color: $secondary-;
        font-weight: 400;
        &:hover{
          color: $primary;
          &:after{
            color: $secondary-;
          }
        }
      }
      &.link-block-primary{
        a{
          color: $primary;
          &:hover{
            color: $secondary-;
          }
        }

      }
    }
    .unit{
      justify-content: center;
      @media (min-width: $screen-md-min){
        justify-content: flex-start;
      }
    }
  }
}

.footer-type-3{
  address{
    color: $gray-lighter-3;
    dd + dt{
      margin-top: 14px;
    }
    dt{
      font-weight: 400;
    }
    dd{
      a{
        color: $gray-lighter-3;
      }
      a[href*='callto']{
        color: $secondary-;
      }
      a:hover{
        color: $primary;
      }
    }
    
  }
  h5 + *{
    margin-top: 30px;
  }
  .logo{
    display: inline-block;
    @media (min-width: $screen-sm-min) {
      margin-top: -30px; 
      img{
        margin-top: -10px;
      }
    }
  }
  a[href*='mailto']{
    font-weight: 700;
    color: $primary;
    &:hover{
      text-decoration: underline;
    }
  }
}

// Typography
//

.section-typography-body{
  h1 + p, h2 + p, h3 + p{
    margin-top: 55px;
  }
  h4 + p, h5 + p, h6 + p{
    margin-top: 32px;
  }
  p + h2,
  p + h3,p + h4,
  p + h5, p + h6{
    margin-top: 60px;
    @media (min-width: $screen-lg-min){
      margin-top: 90px;
    }
  }
}

.heading-group{
  h1 + h2{  margin-top: 30px; }
  h2 + h3{  margin-top: 25px; }
  h3 + h4{  margin-top: 22px; }
  h4 + h5{  margin-top: 15px; }
  h5 + h6{  margin-top: 17px; }
}

.subheading-text{
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: .1em;
  &-primary{
    color: $primary-;
  }
  &-type-1{
    letter-spacing: .15em;
  }
}
* + .subheading-text{
  margin-top: 20px;
}
* + .subheading-text-type-1{
  margin-top: 0;
}


